"use client";

import { useState } from "react";
import DailyQuests from "@/components/DailyQuests";
import Plan from "@/components/Plan";
import Completed from "@/components/Completed";
import BottomNav from "@/components/BottomNav";
import QuestInfo from "@/components/QuestInfo";

export default function Home() {
  const [started, setStarted] = useState(false);

  if (!started) {
    return (
      <main className="flex flex-col items-center justify-center min-h-screen bg-gradient-to-b from-[#1a1330] to-[#2e2547] text-white px-6">
        <div className="max-w-md text-center">
          <h1 className="text-4xl font-serif mb-4 tracking-wide">
            YOUR <span className="text-purple-400">AWAKENING</span> <br />
            BEGINS <span className="text-purple-400">NOW!</span>
          </h1>
          <p className="mb-8 text-sm text-gray-300">
            Walk your path. Take action. Conquer. Become a legend!
          </p>
          <button
            onClick={() => setStarted(true)}
            className="bg-purple-400 text-black font-semibold rounded-full px-8 py-3 hover:bg-purple-500 transition"
          >
            Get Started
          </button>
        </div>
      </main>
    );
  }

  return (
    <main className="flex flex-col items-center justify-start min-h-screen bg-gradient-to-b from-[#1a1330] to-[#2e2547] text-white px-6 pt-12 pb-20">
      <div className="w-full max-w-md">
        <DailyQuests />
        <Plan />
        <Completed />
        <QuestInfo />
      </div>
      <BottomNav />
    </main>
  );
}
