import React from "react";

interface CompletedExercise {
  name: string;
  description: string;
  progress: string;
  completed?: boolean;
}

export default function Completed() {
  const completedExercises: CompletedExercise[] = [
    { name: "Push-ups", description: "Daily Quest", progress: "100 / 100", completed: true },
    { name: "Diamond Push-ups", description: "Personal Added", progress: "100 / 100" },
  ];

  return (
    <div>
      <h3 className="text-sm text-gray-300 mb-2 tracking-widest">COMPLETED</h3>
      <div className="space-y-2">
        {completedExercises.map((ex) => (
          <div
            key={ex.name}
            className={`border rounded-md p-3 flex justify-between ${
              ex.completed ? "border-purple-500 text-purple-400" : "border-gray-600 text-gray-400"
            }`}
          >
            <div>
              <div className="text-sm font-semibold">{ex.name}</div>
              <div className="text-xs">{ex.description}</div>
            </div>
            <div className="text-xs">{ex.progress}</div>
          </div>
        ))}
      </div>
    </div>
  );
}
