import React, { useState } from "react";

interface Goal {
  name: string;
  progress: string;
  completed: boolean;
}

export default function QuestInfo() {
  const [isOpen, setIsOpen] = useState(false);

  const goals: Goal[] = [
    { name: "Push-ups", progress: "50 / 100", completed: false },
    { name: "Sit-ups", progress: "50 / 100", completed: false },
    { name: "Squats", progress: "50 / 100", completed: false },
    { name: "Running", progress: "5 / 5km", completed: true },
  ];

  return (
    <>
      <button
        onClick={() => setIsOpen(true)}
        className="mt-4 px-4 py-2 bg-purple-600 rounded-md text-white font-semibold"
      >
        Show Quest Info
      </button>

      {isOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-70 flex items-center justify-center z-50 p-4">
          <div className="bg-[#1a1330] border border-gray-600 rounded-md max-w-md w-full p-6 relative text-white">
            <button
              onClick={() => setIsOpen(false)}
              className="absolute top-2 right-2 text-gray-400 hover:text-white font-bold text-xl"
              aria-label="Close"
            >
              &times;
            </button>
            <h2 className="text-lg font-semibold mb-2 flex items-center space-x-2">
              <span className="inline-block bg-gray-700 px-2 py-1 rounded">i</span>
              <span>QUEST INFO</span>
            </h2>
            <p className="mb-4 text-sm">
              [Daily Quest: <strong>Strange Training has Arrived.</strong>]
            </p>
            <h3 className="text-center text-xl underline mb-4">GOAL</h3>
            <ul className="space-y-2 mb-4">
              {goals.map((goal) => (
                <li key={goal.name} className="flex justify-between items-center">
                  <span>{goal.name}</span>
                  <span className="flex items-center space-x-2">
                    <span>[{goal.progress}]</span>
                    <input
                      type="checkbox"
                      checked={goal.completed}
                      readOnly
                      className="w-4 h-4"
                    />
                  </span>
                </li>
              ))}
            </ul>
            <p className="text-xs text-red-400 font-semibold">
              WARNING: Failure to complete the daily quest will result in an appreciate penalty
            </p>
          </div>
        </div>
      )}
    </>
  );
}
